import React, {useContext, useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';
import {stockContext} from './App.js';
import { Nav } from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';

const Dib = styled.div`
	padding: 40px;
`;

const HF = styled.h4`
	font-size: 25px;
	color: ${props => props.color};
`;


 

function Detail(props) {
	
	let [showAlert, changeShowAlert] = useState(true);
	let [inputData, changeInputData] = useState('');
	let [selectTab, changeSelectTab] = useState(0);
	let [cssSwitch, changeCssSwitch] = useState(false);

	let stock = useContext(stockContext);

	useEffect(()=>{
		let timer = setTimeout(()=>{
			changeShowAlert(false)
		}, 2000);
		
		return () => {
			clearTimeout(timer);
		}
	}, [showAlert]);

	let history = useHistory();
	let { id } = useParams();


	let thisGoods = props.goods.find(item => item.id == id);

	return (
		<div className="container">
			<Dib>
				<HF className="red">상세페이지</HF>
			</Dib>

			{/* { inputData }
			<input onChange={(e)=>{changeInputData(e.target.value)}} /> */}

			{
				showAlert === true
				? (
					<div className="my-alert">
						<p>재고가 얼마 남지 않았습니다.</p>
					</div>
				)
				: null
			}
			

			<div className="row">
				<div className="col-md-6">
					<img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
				</div>
				<div className="col-md-6 mt-4">
					<h4 className="pt-5">{thisGoods.title}</h4>
					<p>{thisGoods.content}</p>
					<p>{thisGoods.price}</p>

					<Info stock={props.stock}></Info>

					<button className="btn btn-danger m-1" onClick={()=>{
						props.changeStock([9, 11, 12])
					}}>주문하기</button> 
					<button className="btn btn-danger m-1" onClick={() => {
						history.goBack();
					}}>뒤로가기</button> 
				</div>
			</div>

			<Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
				<Nav.Item>
					<Nav.Link eventKey="link-0" onClick={()=>{ changeCssSwitch(false); changeSelectTab(0); }}>Active</Nav.Link>
				</Nav.Item>
				<Nav.Item>
					<Nav.Link eventKey="link-1" onClick={()=>{ changeCssSwitch(false); changeSelectTab(1); }}>Option 2</Nav.Link>
				</Nav.Item>
			</Nav>

			<CSSTransition in={cssSwitch} classNames="wow" timeout={1500}>
				<TabContent selectTab={selectTab} changeCssSwitch={changeCssSwitch}></TabContent>
			</CSSTransition>

		</div>
	)
}

function TabContent(props) {

	useEffect(()=>{
		props.changeCssSwitch(true);
	})

	let selectTab = props.selectTab;
	if (selectTab === 0) {
		return <div>0번째 내용</div>
	} else if (selectTab === 1) {
		return <div>1번째 내용</div>
	} else if (selectTab === 2) {
		return <div>2번째 내용</div>
	}


}

function Info(props) {
	return (
		<p>재고: {props.stock[0]}</p>
	)
}

export default Detail;