import logo from './logo.svg';
import { Navbar, Nav, NavDropdown, Button, Jumbotron } from 'react-bootstrap';
import './App.css';
import React, { useContext, useState } from 'react';
import GoodsData from './data.js';
import Detail from './Detail.js';
import axios from 'axios';
import { Link, Route, Switch } from 'react-router-dom';

export let stockContext = React.createContext();

function App() {

  let [goods, changeGoods] = useState(GoodsData);
  let [stock, changeStock] = useState([10, 11, 12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">새끼 개발자의 Life is Egg</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail/0">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <Switch>
        <Route exact path="/">
          <Jumbotron className="background">
            <h1>30% Season OFF</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>
          <div className="container">

            <stockContext.Provider value={stock}>
              <div className="row">
                {
                  goods.map( (item, i) => {
                    return (
                      <GoodsInfo item={item} index={i} key={i} />
                    )
                  })
                }
              </div>
            </stockContext.Provider>

            <button className="btn btn-primary" onClick={() => {
              axios.get('https://codingapple1.github.io/shop/data2.json')
              .then((result) => {
                let data = result.data;
                changeGoods([...goods, ...data]);

              })
              .catch(()=>{
                console.log('못가져옴')
              });
            }}>더보기</button>
          </div>

        </Route>
        <Route exact path="/detail/:id">
          <stockContext.Provider value={stock}>
            <Detail goods={goods} stock={stock} changeStock={changeStock}/>
          </stockContext.Provider>
        </Route>

        <Route exact path="/:id">
          <div>여긴어디게</div>
        </Route>
      </Switch>
    </div>
  );
}



function GoodsInfo(props) {

  let stock = useContext(stockContext);

  return (
    <div className="col-md-4">
      <img src={ 'https://codingapple1.github.io/shop/shoes' + (props.index + 1) + '.jpg'} width="100%"/>
      <h4>{ props.item.title }</h4>
      <p>{ props.item.content }</p>
      <p>{ props.item.price }</p>
      {stock[props.index]}
    </div>
  )
}


function Test() {
  return <p>재고</p>
}

export default App;
